#about{
  height: fit-content;
}
.about__container {
  display: grid;
  grid-template-columns: 35% 50%;
  gap: 15%;
}

.about__me {
  width: 100%;
  aspect-ratio: 1/1;
  /* aspect-ratio: 1/1; */
  border-radius: 2rem;
  background: linear-gradient(
    45deg,
    transparent,
    var(--color-primary),
    transparent
  );
  display: grid;
  place-items: center;
}

.about__me img {
  height: 55vh;
  border-radius: 2rem;
  overflow: hidden;
  transform: rotate(13deg);
  transition: var(--transition);
  cursor: pointer;
}

.about__me img:hover {
  transform: rotate(0deg);
}

.about__cards {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1.5rem;
}

.about__card {
  background: var(--color-bg-variant);
  border: 1px solid transparent;
  border-radius: 1rem;
  padding: 2rem;
  text-align: center;
}

.about__card:hover {
  background: transparent;
  border-color: var(--color-primary-variant);
  cursor: default;
}

.about__icon {
  color: var(--color-primary);
  font-size: 1.4rem;
  margin-bottom: 1rem;
}
.about__card h5 {
  font-size: 0.95rem;
}
.about__card small {
  font-size: 0.7rem;
  color: var(--color-light);
}
.about__content p {
  margin: 2rem 0, 2.6rem;
  color: var(--color-light);
}

/* =========> Media Queries (Medium Devices / Tablets) <========== */

@media screen and (max-width: 1024px) {
  #about {
    height: 120vh;
  }
  .about__container {
    grid-template-columns: 1fr;
    gap: 0;
  }

  .about__me {
    width: 50%;
    height: 30rem;
    margin: 2rem auto 4rem;
  }
  .about__me img {
    height: 30rem;
  }

  .about__content p {
    margin: 1rem 0 1.5rem;
  }
  #about {
    height: 150vh;
  }
}

/* =========> Media Queries (Small Devices / Mobiles) <========== */

@media screen and (max-width: 600px) {
  .about__container {
    width: auto;
    display: block;
    margin: auto;
    /* border: 1px solid white; */
  }
  #about {
    height: 210vh;
  }
  .about__me {
    width: 70%;
    height: 30rem;
    margin: 0 auto 3rem;
  }
  .about__cards {
    grid-template-columns: 1fr;
    gap: 1rem;
    padding-left: 4rem;
    /* border: 2px solid red; */
  }
  .about__card {
    width: 80%;
  }

  .about__content {
    text-align: center;
  }

  .about__content p {
    margin: 1.5rem 1rem;
  }
}
